.container {
  margin-left: auto;
  margin-right: auto;
  height: 500px;
  width: 1000px;
}

h1 {
  background-color: black;
  color: darkred;
  font-size: 5em;
  text-align: center;
}

h2 {
  font-size: 2em;
  text-align: center;
}

form {
  margin-left: 250px;
  margin-right: auto;
  width: 1000px;
  height: 100%;
}

.input {
  width: 500px;
  text-align: left;
}

.boton {
  align-content: center;
  width: 180px;
  height: 100%;
}

table {
  background-color: darkgrey;
  height: 100%;
  width: 500px;
  margin: auto;
  text-align: left;
  border-style: solid 2px;
}
